

.all {
    width: 100vw;
    height: 100%;
    background-color: #333333;
    .top {
        background-color: #444444;
        width: 100vw;
        height: 20vw;
    }
    .middle {
        background-color: #555555;
        width: 100vw;
        height: calc(100% - 40vw);
        overflow-y: scroll;
    }
    .bottom {
        background-color: #444444;
        width: 100vw;
        height: 20vw;
    }
}
