:root {
  font-family: "Segoe UI", "Microsoft YaHei UI", sans-serif;
  background-color: #222222;
  color: #BBBBBB;
  font-size: 16px;
  -webkit-tap-highlight-color: transparent;
}
::selection {
  background-color: #DDD;
  color: #111;
}
html {
  height: 100%;
}
body {
  margin: 0;
  height: 100%;
}
p {
  margin: 0;
  padding: 0;
}
